.DropDown {
  display: flex;
  position: relative;
  align-items: center;
  margin-bottom: 1vw;
}

.DropDown select {  
  width: calc(270px + 10vw);
  height: calc(20px + 2vw);
  min-width: 310px;
  padding-right: 2.5vw;
  padding-left: 0.5vw;
  font-size: inherit;
  border-radius: 0.4vw;
  border: 1px solid #848484;
}

.DropDown optgroup option {  
  font-size: calc(10px + 0.2vw);
}

.DropDown button {
  position: absolute;
  right: calc(5px + 0.5vw);
  height: calc(18px + 1.5vw);
  width: calc(18px + 1.5vw);
  display: flex;
  justify-content: center;
  border-radius: 50%;
  line-height: calc(10px + 1.5vw);
  font-size: inherit;
  background-color: #848484;
  color: #fff;
  border: none;
  z-index: 100;
}

.DropDown button:hover  {
  background-color: #848484ff;
}