.app {
  min-height: 100vh;
  max-width: 1400px;
  width: 100vw;
  min-width: 320px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center; 
  color: #525c65;
  font-size: calc(10px + 1vw);
}

.header-nav {
  position: relative;
  padding: 1vw 3vw 2vw 3vw;
  margin-bottom: 2vw;
  background-color: #fff;
  width: 100%;
  border-radius: 0.4vw;
}

.header-nav::after {
  background: linear-gradient(60deg,#af3ad3,#2ac8bb);
  content: '';
  position: absolute;
  height: calc(2px + 0.2vw);
  left: 0;
  right: 0;
  top: 0;
  z-index: 100;
  display: block;  
}

.header-nav ul {
  margin: 0;
  padding: 0;
  display: flex;  
}

.header-nav li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.controls {
  display: flex;
  flex-wrap: wrap;
  margin-top: calc(10px + 1vw);
}

.controls > * {
  margin-right: 1vw;
}

a {
  color: #2b2994;
  font-size: calc(14px + 1vw);
}
