.Input {
  position: relative;
  display: flex;
  align-items: center;
  flex-grow: 0;
  margin-bottom: 1vw;
}

.Input input {
  width: calc(270px + 10vw);
  height: calc(20px + 2vw);
  min-width: 310px;
  padding-right: 2.5vw;
  padding-left: calc(5px + 0.5vw);
  font-size: inherit;
  border-radius: 0.4vw;
  border: 1px solid #848484;
}

.Input button {
  position: absolute;
  right: calc(5px + 0.5vw);
  height: calc(18px + 1.5vw);
  width: calc(18px + 1.5vw);
  display: flex;
  justify-content: center;
  border-radius: 50%;
  line-height: calc(10px + 1.5vw);
  font-size: inherit;
  background-color: #848484;
  color: #fff;
  border: none;
  z-index: 100;
}

.Input button:hover {
  background-color: #848484ff;
}