.NewsListItem {
  margin: 0;
  padding: 0;
  position: relative;
  margin-bottom: 2vw;
  list-style-type: none;
}

.NewsListItem article {
  display: flex;
  box-shadow: 5px 5px 20px 0 rgba(45, 62, 71, 18%);
  border-radius: calc(4px + 0.4vw);
  border: 1px solid #dbe0e6;
  background-color: #fff;
  font-size: inherit;
}

.NewsListItem__image-large {
  width: 30%;  
  border-radius: inherit;
}

.NewsListItem__image-large img {
  object-fit: cover;
  object-position: 50% 50%;
  width: 100%;
  height: 100%;
  border-bottom-left-radius: inherit;
  border-top-left-radius: inherit;
}

.NewsListItem__details {
  width: 70%;
  padding: 2vw;
  display: flex;
  flex-direction: column;
}

.NewsListItem h1 {
  margin-top: 0;
  font-size: calc(14px + 2vmin);
}

.NewsListItem a {
  margin-top: 1vw;
  color: rgb(15, 95, 141)
}

.NewsListItem__image-small img {
  display: none;
}


@media screen and (max-width: 620px) {
  .NewsListItem__image-large {
    display: none;
  }

  .NewsListItem__details {
    padding: 0;    
    width: 100%;
  }

  .NewsListItem__misc {
    position: absolute;
    right: 2vw;
    top: 2vw;
    padding: 1vw;
    background: #000000bb;
    color: #fff;
    border-radius: calc(4px + 0.4vw);
  }
  
  .NewsListItem__image-small img {
    display: block;
    border-top-left-radius: calc(4px + 0.4vw);
    border-top-right-radius: calc(4px + 0.4vw);
    object-fit: cover;
    object-position: 50%;
    width: 100%;
    height: 40vw;
  }

  .NewsListItem h1 {
    margin-top: 14px;
    margin: 1vw 4vw;
  }

  .NewsListItem p {
    font-size: 20px;
    margin: 3vw 4vw;
  }

  .NewsListItem a {    
    margin: 1vw 4vw 4vw 4vw;
    color: rgb(15, 95, 141);
    font-size: 24px;
  }
}